<template>
  <div id="product-list-one">
    <h2>Product List One</h2>

    <ul>
      <li v-for="(product, index) in saleProducts" :key="index">
        <span class="name">{{ product.name }}</span>
        <span class="price">${{ product.price }}</span>
      </li>
    </ul>

    <button v-on:click="reducePrice(5)">Reduce Price</button>
  </div>
</template>

<script>
// Menggunakan mapGetters dan mapActions untuk mengambil fungsi yang ada di store
// tanpa perlu menggunakan this.$store.

import { mapGetters, mapActions } from "vuex";

export default {
  // Gunakan computed jika data berasal dari luar atau bergantung pada state.

  computed: {
    // Mengambil products dari getters yang berada store.

    products() {
      return this.$store.state.products;
    },
    // Mengambil saleProducts dari getters yang berada di store.

    ...mapGetters(["saleProducts"])
  },
  methods: {
    // Melakukan dispatch untuk sebuah action dengan nama 'reducePrice' yang ada di store.
    // Amount adalah payload yang ada pada store.

    ...mapActions(["reducePrice"])
  }
};
</script>

<style scoped>
#product-list-one {
  background: #fff8b1;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.2);
  margin-bottom: 30px;
  padding: 10px 20px;
}

#product-list-one ul {
  padding: 0;
}

#product-list-one li {
  display: inline-block;
  margin-right: 10px;
  margin-top: 10px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.7);
}

.name {
  margin-right: 0.5rem;
}

.price {
  font-weight: bold;
  color: #e8800c;
}
</style>
