/* eslint-disable */

import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    products: [
      { name: "Banana Skin", price: 20 },
      { name: "Shiny Star", price: 40 },
      { name: "Green Shells", price: 60 },
      { name: "Red Shells", price: 80 },
    ],
  },
  getters: {
    // Return products dengan nama ditambah '*' dan harga yang sudah didiskon.

    saleProducts: (state) => {
      return state.products.map((product) => {
        return { name: `**${product.name}**`, price: product.price / 2 };
      });
    },
  },
  mutations: {
    // Mengubah products secara langsung dengan cara mengurangi harganya sebanyak payload.
    // Payload adalah value yang dikirimkan.
    // Contoh: payload = 5. Maka akan dikurangi 5.

    reducePrice: (state, payload) => {
      state.products.forEach((product) => (product.price -= payload));
    },
  },
  actions: {
    // Melakukan commit pada sebuah mutation dengan nama 'reducePrice'.
    // setTimeout mewakili async yang akan tetap berjalan meski task belum selesai.
    // Akan sulit untuk melakukan tracking jika commit dilakukan secara langsung tanapa menggunakan action.

    // Payload adalah value yang dikirimkan.

    reducePrice: (context, payload) => {
      setTimeout(() => {
        context.commit("reducePrice", payload);
      }, 3000);
    },
  },
  modules: {},
});
